import { useEffect, useState } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { FormattedMessage } from 'react-intl';

import './style.scss';

const TakeoverDialog = (props) => {
  const timeoutInSeconds = 30;
  const isActive = props.open;
  const onAccept = props.onAccept;
  const [timeLeft, setTimeLeft] = useState(timeoutInSeconds);

  useEffect(() => {
    let interval = null;

    if (isActive) {
      interval = setInterval(() => {
        setTimeLeft((timeLeft) => timeLeft - 1);
      }, 1000);

      if (timeLeft <= 0) {
        onAccept();
        clearInterval(interval);
        setTimeLeft(timeoutInSeconds);
      }
    } else {
      clearInterval(interval);
      setTimeLeft(timeoutInSeconds);
    }
    return () => clearInterval(interval);
  }, [isActive, onAccept, timeLeft]);

  return (
    <Dialog
      open={props.open}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">{props.title}</DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">{props.contentText}</DialogContentText>
      </DialogContent>
      <Button onClick={props.onAccept} color="primary">
        <FormattedMessage id="accept" />
      </Button>
      <Button onClick={props.onReject} color="primary">
        <FormattedMessage id="reject" />
      </Button>
      <div className="auto-accept-container">
        <p>(auto accept in {timeLeft} seconds)</p>
      </div>
    </Dialog>
  );
};

export default TakeoverDialog;
