import React, { useState, useRef } from 'react';
import { FormattedMessage } from 'react-intl';
import { useSelector } from 'react-redux';
import { withRouter, Redirect } from 'react-router-dom';

import DownloadIcon from '../../../../assets/icons/download.svg';
import HelpIcon from '../../../../assets/icons/help.svg';
import AboutIcon from '../../../../assets/icons/info.svg';
import LogoutIcon from '../../../../assets/icons/logout.svg';
import PermissionsIcon from '../../../../assets/icons/permissions.svg';
import ShortcutHelpIcon from '../../../../assets/icons/shortcut-help.svg';
import UploadIcon from '../../../../assets/icons/upload.svg';
import UserIcon from '../../../../assets/icons/user.svg';
import MenuList from '../../../../components/MenuList';
import { forceDownload } from '../../../../utils/downloader';
import {
  canReadUserManagement,
  canEditUserManagement,
  canReadSVOConfiguration,
  canWriteSVOConfiguration,
} from '../../../../utils/permissions';
import AboutView from '../../../AboutView';
import HelpView from '../../../HelpView';
import ShortcutView from '../../../ShortcutView';

import './style.scss';

const UserInfo = () => {
  const user = useSelector((state) => state.app.user);
  const myPermissions = useSelector((state) => state.app.myPermissions);
  const proxy = useSelector((state) => state.app.proxy);
  const inputFile = useRef(null);

  const [shortcutViewOpen, setShortcutViewOpen] = useState(false);
  const [isHelpViewOpen, setIsHelpViewOpen] = useState(false);
  const [isAboutViewOpen, setIsAboutViewOpen] = useState(false);
  const [gotoPermissions, setGotoPermissions] = useState(false);

  const downloadConfig = async () => {
    const fileData = await proxy.downloadSVOConfig();
    const blob = new Blob([fileData], { type: 'application/octet-stream' });
    const url = URL.createObjectURL(blob);
    forceDownload(url, `SVOConfig_${new Date().toISOString()}.svz`);
  };

  const menuItems = [
    {
      key: 'help',
      caption: <FormattedMessage id="topNavigation.help" />,
      img: HelpIcon,
      onClick: () => {
        setIsHelpViewOpen(true);
      },
    },
    {
      key: 'shortcuthelp',
      caption: <FormattedMessage id="topNavigation.shortcut" />,
      img: ShortcutHelpIcon,
      onClick: () => {
        setShortcutViewOpen(!shortcutViewOpen);
      },
    },
    {
      key: 'about',
      caption: <FormattedMessage id="topNavigation.about" />,
      img: AboutIcon,
      onClick: () => {
        setIsAboutViewOpen(true);
      },
    },
    {
      key: 'logout',
      caption: <FormattedMessage id="topNavigation.logout" />,
      img: LogoutIcon,
      onClick: async () => {
        setImmediate(() => (window.location.href = `${window.location.origin}/logout`));
      },
    },
  ];

  const onFileAdded = (event) => {
    const file = event.target.files[0];
    if (file) {
      const fileReader = new FileReader();
      fileReader.readAsArrayBuffer(file);

      fileReader.onload = (event) => {
        const arrayBuffer = event.target.result;
        const array = new Uint8Array(arrayBuffer);
        proxy.uploadSVOConfig(file.name, array);
      };
    }
  };

  if (canWriteSVOConfiguration(myPermissions)) {
    menuItems.unshift({
      key: 'upload',
      caption: <FormattedMessage id="topNavigation.upload" />,
      img: UploadIcon,
      onClick: async () => inputFile.current.click(),
    });
  }

  if (canReadSVOConfiguration(myPermissions)) {
    menuItems.unshift({
      key: 'download',
      caption: <FormattedMessage id="topNavigation.download" />,
      img: DownloadIcon,
      onClick: () => downloadConfig(),
    });
  }

  if (canReadUserManagement(myPermissions) || canEditUserManagement(myPermissions)) {
    menuItems.unshift({
      key: 'permissions',
      caption: <FormattedMessage id="topNavigation.permissions" />,
      img: PermissionsIcon,
      onClick: () => {
        setGotoPermissions(true);
      },
    });
    if (gotoPermissions) {
      return <Redirect to="/permissions" />;
    }
  }

  return (
    <div>
      <input
        type="file"
        id="file"
        ref={inputFile}
        style={{ display: 'none' }}
        onChange={onFileAdded}
      />
      <div className="flex">
        <MenuList
          buttonIcon={UserIcon}
          buttonCaption={user.fullname}
          menuItems={menuItems}
          showBottomArrow
          onClose={() => void 0}
          open={false}
        />
      </div>
      <div>
        <HelpView open={isHelpViewOpen} onClose={() => setIsHelpViewOpen(false)} />
        <ShortcutView open={shortcutViewOpen} onClose={() => setShortcutViewOpen(false)} />
        <AboutView open={isAboutViewOpen} onClose={() => setIsAboutViewOpen(false)} />
      </div>
    </div>
  );
};

export default withRouter(UserInfo);
