import { useReducer, useState } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import TextField from '@material-ui/core/TextField';
import { FormattedMessage } from 'react-intl';
import { useSelector } from 'react-redux';

import lockIcon from '../../../../../src/assets/icons/icons8-lock.svg';
import padlockIcon from '../../../../../src/assets/icons/icons8-padlock.svg';

import AlertDialog from './alertDialog';
import TakeoverDialog from './takeoverDialog';

import './style.scss';

const formReducer = (state, event) => {
  return {
    ...state,
    [event.name]: event.value,
  };
};

const LockIcon = () => {
  return <img src={lockIcon} alt="Lock/Takeover the configuration" title="Lock/Takeover config" />;
};

const PadlockIcon = () => {
  return <img src={padlockIcon} alt="Release the configuration lock" title="Release config" />;
};

const LockStateHandler = () => {
  const proxy = useSelector((state) => state.app.proxy);
  const configLock = useSelector((state) => state.app.configurationLock);
  const loggedUser = useSelector((state) => state.app.user);
  const [openFormDialog, setOpenFormDialog] = useState(false);
  const [formData, setFormData] = useReducer(formReducer, { scope: 1 });

  const handleChange = (event) => {
    setFormData({
      name: event.target.name,
      value: event.target.value,
    });
  };

  const handleClick = () => {
    if (configLock.isLockedBySameUser(loggedUser)) {
      proxy.unsubscribeFromLockAcquisitionStream();
    } else {
      setOpenFormDialog(true);
    }
  };

  const handleClose = () => {
    setOpenFormDialog(false);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setOpenFormDialog(false);
    formData.requestTakeover = configLock.isLockedByOtherUser(loggedUser);
    proxy.subscribeToLockAcquisitionStream(formData);
  };

  const renderAlertDialogs = () => {
    return (
      <>
        <AlertDialog
          open={configLock.isConfigAlreadyLocked()}
          title={
            <FormattedMessage
              id="lockStateHandler.alertDialog.lockAlreadyLocked"
              values={{ user: configLock.owner }}
            />
          }
          contentText={
            <FormattedMessage
              id="lockStateHandler.alertDialog.lockDescription"
              values={{ description: configLock.lockDescription }}
            />
          }
        >
          <Button
            onClick={() => {
              proxy.closeAlertDialogs();
            }}
            color="primary"
          >
            <FormattedMessage id="close" />
          </Button>
        </AlertDialog>
        <AlertDialog
          open={configLock.isOwnerRejectedTakeover()}
          title={
            <FormattedMessage
              id="lockStateHandler.alertDialog.ownerRejectedTakeover"
              values={{ user: configLock.owner }}
            />
          }
          contentText={
            <FormattedMessage
              id="lockStateHandler.alertDialog.lockDescription"
              values={{ description: configLock.lockDescription }}
            />
          }
        >
          <Button
            onClick={() => {
              proxy.closeAlertDialogs();
            }}
            color="primary"
          >
            <FormattedMessage id="close" />
          </Button>
        </AlertDialog>
        <AlertDialog
          open={configLock.isWaitingForTakeoverResponse()}
          title={
            <FormattedMessage
              id="lockStateHandler.alertDialog.waitingForTakeoverResponse"
              values={{ user: configLock.owner }}
            />
          }
          contentText={
            <FormattedMessage id="lockStateHandler.alertDialog.waitingForResponseInfo" />
          }
        ></AlertDialog>
        <TakeoverDialog
          open={configLock.isTakeoverAttemptFromOtherUser()}
          title={
            <FormattedMessage
              id="lockStateHandler.alertDialog.takeoverAttempt"
              values={{ user: configLock.takeoverUser }}
            />
          }
          contentText={
            <FormattedMessage
              id="lockStateHandler.alertDialog.takeoverMessage"
              values={{ message: configLock.takeoverMessage }}
            />
          }
          onAccept={() => {
            proxy.passConfigLock({
              lockId: configLock.lockId,
              takeoverId: configLock.takeoverId,
            });
          }}
          onReject={() => {
            proxy.retainConfigLock({ takeoverId: configLock.takeoverId });
          }}
        ></TakeoverDialog>
      </>
    );
  };

  const renderFormDialog = () => {
    return (
      <Dialog open={openFormDialog} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">
          {configLock.isLockedByOtherUser(loggedUser) ? (
            <FormattedMessage id="lockStateHandler.formDialog.takeoverConfigRequestTitle" />
          ) : (
            <FormattedMessage id="lockStateHandler.formDialog.lockConfigRequestTitle" />
          )}
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <FormattedMessage id="lockStateHandler.formDialog.lockConfigRequestContent" />
          </DialogContentText>
          <TextField
            InputLabelProps={{ shrink: true }}
            autoFocus
            margin="dense"
            id="lock-scope-id"
            name="scope"
            label={<FormattedMessage id="lockStateHandler.formDialog.lockScope" />}
            type="number"
            defaultValue="1"
            disabled={true}
            onChange={handleChange}
            fullWidth
          />
          <TextField
            InputLabelProps={{ shrink: true }}
            margin="dense"
            id="lock-description-id"
            name="description"
            label={<FormattedMessage id="lockStateHandler.formDialog.lockDescription" />}
            type="text"
            onChange={handleChange}
            fullWidth
          />
          {configLock.isLockedByOtherUser(loggedUser) && (
            <>
              <TextField
                InputLabelProps={{ shrink: true }}
                margin="dense"
                id="takeover-message-id"
                name="takeoverMessage"
                label={<FormattedMessage id="lockStateHandler.formDialog.takeoverMessage" />}
                type="text"
                onChange={handleChange}
                fullWidth
              />
            </>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            <FormattedMessage id="cancel" />
          </Button>
          <Button onClick={handleSubmit} color="primary">
            <FormattedMessage id="submit" />
          </Button>
        </DialogActions>
      </Dialog>
    );
  };

  const renderLabelBasedOnLockStatus = () => {
    if (configLock.isLockedBySameUser(loggedUser)) {
      return <FormattedMessage id="topNavigation.release" />;
    } else if (configLock.isLockedByOtherUser(loggedUser)) {
      return <FormattedMessage id="topNavigation.takeover" />;
    } else {
      return <FormattedMessage id="topNavigation.lock" />;
    }
  };

  return (
    <div className="lock-state-handler">
      <div role="button" tabIndex="0" className="lock-config-button" onClick={handleClick}>
        {configLock.isLockedBySameUser(loggedUser) ? <PadlockIcon /> : <LockIcon />}
        <p>{renderLabelBasedOnLockStatus()}</p>
      </div>
      <div className="lock-status">
        <p>Status: {configLock.isLocked() ? 'Locked' : 'Unlocked'}</p>
        <p>User: {configLock.owner}</p>
        <p>Host: {configLock.isLocked() ? configLock.host : '0.0.0.0'}</p>
      </div>
      {renderFormDialog()}
      {renderAlertDialogs()}
    </div>
  );
};

export default LockStateHandler;
